#generate {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 1%;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 50px;
    width: 50px;
    height: 50px;
}
#generate:hover {
    background-color: rgb(24, 146, 71);
    transition: all 0.3s;
}
.e-schedule .e-vertical-view .e-work-cells {
    background-color: #EEF;
}

.schedule-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 10vh;
    background-color: lightblue;
}

.schedule-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.schedule-nav button {
    padding: 10px;
    margin: 10px;
}

